import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';
import { ChartsModule } from 'ng2-charts/ng2-charts';
import { MdTableModule } from '@angular/material';

// Components
import { DashboardComponent } from '../../../components/dashboard-components/dashboard/dashboard.component';
import { ViewTitleDirectiveModule } from '../../directive-modules/view-title-directive/view-title-directive.module';
import { NotificationDirectiveModule } from '../../directive-modules/notification-directive/notification-directive.module';

// Services
import { DashboardService } from '../../../services/dashboard/dashboard.service';
import { NotificationsService } from '../../../services/notifications/notifications.service';

// Pipes
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';
import { CdkTableModule } from '@angular/cdk/table';


@NgModule({
  imports: [
    AngularMaterialModule,
    CommonModule,
    ViewTitleDirectiveModule,
    NotificationDirectiveModule,
    CapitalizePipeModule,
    ChartsModule,
    MdTableModule,
    CdkTableModule,
  ],
  declarations: [
    DashboardComponent,
  ],
  exports: [
    DashboardComponent,
  ],
  providers: [
    DashboardService,
    NotificationsService,
  ],
})
export class DashboardModule { }



