import { Component, OnInit } from '@angular/core';
import { TeamsService } from '../../../services/teams/teams.service';
import { UsersService } from '../../../services/users/users.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import { User } from '../../../classes/user';
import { Team } from '../../../classes/team';
import { Observable } from 'rxjs/Observable';
import * as _ from 'underscore';

@Component({
  selector: 'app-team',
  templateUrl: './team.component.html',
  styleUrls: ['./team.component.scss'],
})
export class TeamComponent implements OnInit {
  defaultPhoto: string = '../../../assets/default_image.png';
  defaultImage: string = '../../../assets/default_user.png';
  tituloVista: string = 'Team';
  subtituloVista: string;
  action: string;
  team: Team = new Team();
  usersGroup: any = [];

  constructor(
    private TeamsService: TeamsService,
    private UsersService: UsersService,
    private router: Router,
    private route: ActivatedRoute,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.startLoading();
    this.route.params.subscribe(params => {
      this.subtituloVista = this.action = params['action'];
      Observable.combineLatest(
        this.UsersService.getAllWithoutPhoto(),
      ).subscribe(data => {
        this.getUsers(data[0]);
        const kActions = {
          ['create']:() => this.completeLoading(),
          ['view']:() => this.getTeam(params['id']),
          ['edit']:() => this.getTeam(params['id']),
        };
        kActions[this.action]();
      },
      error => {
        this.completeLoading();
        this.openSnackBar(error.statusText, true);
      });

    });
  }

  private getUsers(users) {
    this.usersGroup = _.groupBy(users, 'rank');
    console.log(this.usersGroup);
  }

  private getTeam(teamID) {
    this.TeamsService.get(teamID)
    .subscribe(team => {
      console.log(team);
      this.team = team;
      this.team.creationDate = this.utcToDate(this.team.creationDate);
      this.team.modificationDate = this.utcToDate(this.team.modificationDate);
      this.team['creationDateString'] = this.team.creationDate ? this.team.creationDate.toLocaleString() : '-';
      this.team['modificationDateString'] = this.team.modificationDate ? this.team.modificationDate.toLocaleString() : '-';
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private utcToDate(utc) {
    return utc ? new Date(Date.parse(utc)) : null;
  };

  public isEditForm() {
    return this.action !== 'view';
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public upload(res, type) {
    this.readImage(res.target, type);
  }

  private readImage(inputValue: any, type: string): void {
    const file: File = inputValue.files[0];
    const myReader: FileReader = new FileReader();

    myReader.onloadend = (e) => {
      if(type === 'photo') {
        this.team.teamPhoto64 = myReader.result;
      } else {
        this.team.teamLogo64 = myReader.result;
      }
    };
    myReader.readAsDataURL(file);
  }

  public addUsers() {
    this.openSnackBar('Operation not available', null);
  }

  public addProjects() {
    this.openSnackBar('Operation not available', null);
  }

  public saveTeam() {
    const kSave = {
      ['create']:() => this.createTeam(),
      ['edit']:() => this.updateTeam(),
      ['view']:() => this.editTeam(),
    };
    kSave[this.action]();
  }

  private createTeam() {
    this.startLoading();
    this.team.creationDate = new Date();

    this.TeamsService.create(this.team)
    .subscribe(result => {
      this.openSnackBar('Team «' + result.name + '» created correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private updateTeam() {
    this.startLoading();
    this.team.teamPhoto = {};
    this.team.teamLogo = {};
    this.team.modificationDate = new Date();

    this.TeamsService.update(this.team)
    .subscribe(result => {
      this.openSnackBar('Team «' + result.name + '» updated correctly', null);
      this.completeLoading();
      this.viewTeam();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private editTeam() {
    this.router.navigate(['/team', 'edit', this.team._id]);
  }

  private viewTeam() {
    this.router.navigate(['/team', 'view', this.team._id]);
  }

  public deleteTeam(id) {
    this.startLoading();
    this.TeamsService.delete(id)
    .subscribe(result => {
      this.openSnackBar('Team «' + result.name + '» deleted correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteTeam() {
    this.openSnackBar('Cancelled operation', null);
  }

  private back() {
    this.router.navigate(['/teams']);
  }
}



