export class Meeting {
    _id: string;
    name: string;
    description: string;
    place: string;
    creationDate: Date;
    modificationDate: Date;
    initDate: Date;
    initTime: any;
    finalDate: Date;
    finalTime: any;
    users: any;    
    topics: any;
    comments: any;
    meetingManager: any;
    teams: any;
    tasks: any;
    meetingPhoto: any;
    meetingPhoto64: string;

    //calendar related
    color: string;
    allDay: boolean;
    beforeStart: boolean;
    afterEnd: boolean; 
    draggable: boolean; 

}


