import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';
import { CalendarModule } from 'angular-calendar';

// Components
import { CalendarComponent } from '../../../components/calendar-components/calendar/calendar.component';
import { ViewTitleDirectiveModule} from '../../directive-modules/view-title-directive/view-title-directive.module';

// Services
import { CalendarService } from '../../../services/calendar/calendar.service';

// Pipes
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';

@NgModule({
  imports: [
    AngularMaterialModule, 
    CommonModule,
    CalendarModule.forRoot(),
    ViewTitleDirectiveModule,
    CapitalizePipeModule,
  ],
  declarations: [
    CalendarComponent,
  ],
  exports: [
    CalendarComponent,
  ],
  providers: [
    CalendarService,
  ],
})
export class Calendar2Module { }



