import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';
import { MdTableModule } from '@angular/material';
import { CdkTableModule } from '@angular/cdk/table';

// Components
import { ProfileComponent } from '../../../components/profile-components/profile/profile.component';
import { ViewTitleDirectiveModule} from '../../directive-modules/view-title-directive/view-title-directive.module';

// Services
import { ProfileService } from '../../../services/profile/profile.service';

//Pipes
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';


@NgModule({
  imports: [
    AngularMaterialModule, 
    CommonModule,
    FormsModule,    
    ViewTitleDirectiveModule,
    CapitalizePipeModule,
    MdTableModule,
    CdkTableModule,
  ],
  declarations: [
    ProfileComponent,
  ],
  exports: [
    ProfileComponent,
  ],
  providers: [
    ProfileService,
  ],
})
export class ProfileModule { }



