// Angular
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MdSnackBar } from '@angular/material';

import { LoginService } from '../../../services/login/login.service';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { User } from '../../../classes/user';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  appLogo: string = '../../../assets/icon.png';
  user: User = new User();
  isLogged: boolean = false;
  login: boolean = false;

  constructor(
    private router: Router,
    private LoginService: LoginService,
    public snackBar: MdSnackBar,
    public slimLoadingBarService: SlimLoadingBarService,
  ) { }

  ngOnInit() {
    this.setBackground(true);
  }

  private setBackground(image) {
    var body = document.getElementsByTagName('body')[0];
    body.style.background = image ? 'url(../assets/background.jpg)' : '';
    body.style.backgroundRepeat = 'repeat';
    body.style.backgroundSize = 'cover';
  }

  public doLogin() {
    this.login = true;
    this.startLoading();
    this.LoginService.login(this.user)
    .subscribe(logged => {
      this.completeLoading();
      if(logged) {
        this.doneLogin();
        this.snackBar.dismiss();
      }
      else {
        this.openSnackBar('Login Failed', true);
      }
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    }, () => this.login = false);
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  public isLoggin() {
    return this.login;
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  private doneLogin() {
    this.setBackground(null);
    this.router.navigate(['/dashboard']);
  }
}



