// Angular
import { HttpModule} from '@angular/http';
import { FormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { RouterModule } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SlimLoadingBarModule } from 'ng2-slim-loading-bar';
import { CdkTableModule } from '@angular/cdk/table';
// import { MAT_DATE_LOCALE } from '@angular/material';

// Insights
import { ApplicationInsightsModule, AppInsightsService } from '@markpieszak/ng-application-insights';

// Angular Material
import { AngularMaterialModule } from './modules/angular-material/angular-material.module';
import 'hammerjs';

// Directives
import { FooterBarComponent } from './directives/footer-bar/footer-bar.component';
import { NavigationComponent } from './directives/navigation/navigation.component';

// Routes
import { RouteDefinitions } from './routes/route-definitions';

// Modules
import { ViewTitleDirectiveModule} from './modules/directive-modules/view-title-directive/view-title-directive.module';
import { DashboardModule} from './modules/component-modules/dashboard/dashboard.module';
import { TeamsModule} from './modules/component-modules/teams/teams.module';
import { UsersModule} from './modules/component-modules/users/users.module';
import { TasksModule} from './modules/component-modules/tasks/tasks.module';
import { ProjectsModule} from './modules/component-modules/projects/projects.module';
import { ReportsModule} from './modules/component-modules/reports/reports.module';
import { MeetingsModule} from './modules/component-modules/meetings/meetings.module';
import { Calendar2Module} from './modules/component-modules/calendar/calendar2.module';
import { LoginModule} from './modules/component-modules/login/login.module';
import { SearchModule} from './modules/component-modules/search/search.module';
import { ProfileModule} from './modules/component-modules/profile/profile.module';
import { NotificationsModule} from './modules/component-modules/notifications/notifications.module';
import { MessengerModule} from './modules/component-modules/messenger/messenger.module';
import { CapitalizePipeModule} from './modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';
import { ObjectNgForPipeModule} from './modules/pipe-modules/objectNgFor-pipe/objectNgFor-pipe.module';

@NgModule({
  declarations: [
    AppComponent,
    NavigationComponent,
    FooterBarComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    RouterModule.forRoot(RouteDefinitions),
    FormsModule,
    HttpModule,
    AngularMaterialModule,
    SlimLoadingBarModule.forRoot(),
    CdkTableModule,
    // Modules Components
    DashboardModule,
    TeamsModule,
    UsersModule,
    TasksModule,
    ProjectsModule,
    ReportsModule,
    MeetingsModule,
    Calendar2Module,
    LoginModule,
    SearchModule,
    ProfileModule,
    NotificationsModule,
    MessengerModule,
    // Modules directives
    ViewTitleDirectiveModule,
    // Modules pipes
    CapitalizePipeModule,
    ObjectNgForPipeModule,
    // Insights
    ApplicationInsightsModule.forRoot({
      instrumentationKey: '8a9ee28c-652e-4e1b-8c62-c7db88a30ba1'
    }),
  ],
  bootstrap: [
    AppComponent
  ],
  providers: [
    AppInsightsService,
    // {provide: MAT_DATE_LOCALE, useValue: 'en-GB'},
  ],

})
export class AppModule { }



