import { ProjectsService } from '../../../services/projects/projects.service';
import { trigger, transition, style, animate, state } from '@angular/animations'
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import * as _ from 'underscore';
import { Project } from '../../../classes/project';
import { imageAnimation } from '../../../animations/animations';

const notFound: string = 'Not found projects';
const getting: string = 'Getting projects';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss'],
  animations: [imageAnimation],
})
export class ProjectsComponent implements OnInit {
  defaultImage: string = '../../../assets/default_image.png';
  tituloVista: string = 'Projects';
  infoMessage: string;
  projects: any = [];

  constructor(
    private ProjectsService: ProjectsService,
    private router: Router,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.infoMessage = getting;
    // Start loading bar
    this.startLoading();
    this.ProjectsService.getAllWithoutPhoto()
    .subscribe(projects => {
      this.infoMessage = notFound;
      this.projects = projects;
      console.log(projects);

      this.getProjectPhotos();

      // Finish loading bar
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }


  private getProjectPhotos(){
    let projectService = this.ProjectsService;
    _.each(this.projects, function(project: Project){
        projectService.getPhoto(project._id)
        .subscribe(photo => {
          project.projectPhoto64 = photo.projectPhoto64;
        });
    }); 
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public actionProject(action, project) {
    var params = [
      '/project',
      action
    ];
    if(action !== 'create') {
      params = params.concat([project._id]);
    }
    this.router.navigate(params);
  }

  public deleteProject(id) {
    this.startLoading();
    this.ProjectsService.delete(id)
    .subscribe(result => {
      this.projects = _.without(this.projects, _.findWhere(this.projects, {_id: result._id}));
      this.openSnackBar('Project «' + result.name + '» deleted correctly', null);
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteProject() {
    this.openSnackBar('Cancelled operation', null);
  }
}



