import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';

// Components
import { ProjectsComponent } from '../../../components/project-components/projects/projects.component';
import { ProjectComponent } from '../../../components/project-components/project/project.component';

// Modules
import { ViewTitleDirectiveModule} from '../../../modules/directive-modules/view-title-directive/view-title-directive.module';
import { DeleteConfirmationDirectiveModule } from '../../../modules/directive-modules/delete-confirmation-directive/delete-confirmation-directive.module';
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';

// Services
import { ProjectsService } from '../../../services/projects/projects.service';

const ProjectComponents = [
  ProjectComponent,
  ProjectsComponent,
];

@NgModule({
  imports: [
    AngularMaterialModule,
    CommonModule,
    ViewTitleDirectiveModule,
    FormsModule,
    DeleteConfirmationDirectiveModule,
    CapitalizePipeModule,
  ],
  declarations: [
    ProjectComponents,
  ],
  exports: [
    ProjectComponents,
  ],
  providers: [
    ProjectsService,
  ],
})
export class ProjectsModule { }



