import { TeamsService } from '../../../services/teams/teams.service';
import { Component, OnInit } from '@angular/core';
import { trigger, transition, style, animate, state } from '@angular/animations'
import { Router } from '@angular/router';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import * as _ from 'underscore';
import { TeamsModule } from '../../../modules/component-modules/teams/teams.module';
import { Team } from '../../../classes/team';
import { imageAnimation } from '../../../animations/animations';

const notFound: string = 'Not found teams';
const getting: string = 'Getting teams';

@Component({
  selector: 'app-teams',
  templateUrl: './teams.component.html',
  styleUrls: ['./teams.component.scss'],
  animations: [imageAnimation],
})
export class TeamsComponent implements OnInit {
  defaultImage: string = '../../../assets/default_image.png';
  tituloVista: string = 'Teams';
  infoMessage: string;
  teams: any = [];

  constructor(
    private TeamsService: TeamsService,
    private router: Router,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.infoMessage = getting;
    // Start loading bar
    this.startLoading();
    this.TeamsService.getAll()
    .subscribe(teams => {
      this.infoMessage = notFound;
      this.teams = teams;
      this.getTeamPhotos();
      // Finish loading bar
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private getTeamPhotos(){
    let teamService = this.TeamsService;
    _.each(this.teams, function(team: Team){
        teamService.getPhoto(team._id)
        .subscribe(photo => {
          team.teamPhoto64 = photo.teamPhoto64;
        });
    });
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public actionTeam(action, team) {
    var params = [
      '/team',
      action
    ];
    if(action !== 'create') {
      params = params.concat([team._id]);
    }
    this.router.navigate(params);
  }

  public deleteTeam(id) {
    this.startLoading();
    this.TeamsService.delete(id)
    .subscribe(result => {
      this.teams = _.without(this.teams, _.findWhere(this.teams, {_id: result._id}));
      this.openSnackBar('Team «' + result.name + '» deleted correctly', null);
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteTeam() {
    this.openSnackBar('Cancelled operation', null);
  }
}



