// Angular
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { MdSnackBar } from '@angular/material';

import { CalendarEvent, CalendarMonthViewDay, CalendarEventTimesChangedEvent } from 'angular-calendar';
import { Subject } from 'rxjs/Subject';
import { startOfDay, endOfDay, subDays, addDays, endOfMonth, isSameDay, isSameMonth, addHours} from 'date-fns';
import { MonthNames } from '../../../enums/monthNames.enum';
import { MeetingsService } from '../../../services/meetings/meetings.service';
import { Meeting } from '../../../classes/meeting';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import * as _ from 'underscore';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})
export class CalendarComponent implements OnInit {
  tituloVista: string = 'Calendar';
  viewDate: Date = new Date();
  monthNames = MonthNames;
  month = this.monthNames[this.viewDate.getMonth()]
  year = this.viewDate.getFullYear();
  meetings: any = [];
  events: any = [];
  refresh: Subject<any> = new Subject();

  // export interface CalendarEvent {
  //     start: Date;
  //     end?: Date;
  //     title: string;
  //     color: EventColor;
  //     actions?: EventAction[];
  //     allDay?: boolean;
  //     cssClass?: string;
  //     resizable?: {
  //         beforeStart?: boolean;
  //         afterEnd?: boolean;
  //     };
  //     draggable?: boolean;
  // }

  constructor(
    public router: Router,
    public MeetingsService: MeetingsService,
    public slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar,
  ) { }

  ngOnInit() {
    this.startLoading();
    this.MeetingsService.getAll()
    .subscribe(meetings => {
      this.meetings = meetings;
      _.each(meetings, (meeting: Meeting) => {
        this.events.push({
          _id: meeting._id,
          title: meeting.name,
          start: this.toDate(meeting.initDate, meeting.initTime),
          end: this.toDate(meeting.finalDate, meeting.finalTime),
          color: {
            primary: meeting.color || '#71ADB5'
          },
          draggable: true,
          resizable: {
              beforeStart: true,
              afterEnd: true,
          },
        });
      });
      this.refresh.next();
      // Finish loading bar
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private toDate(dateUTC, timeString) {
    var date = _.isDate(dateUTC) ? dateUTC : new Date(Date.parse(dateUTC));
    var timeSplit = timeString.split(':');
    date.setHours(timeSplit[0]);
    date.setMinutes(timeSplit[1]);
    return date;
  };

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public previusMonth(){
    this.viewDate = new Date(new Date(this.viewDate).setMonth(this.viewDate.getMonth()-1));
    this.month = this.monthNames[this.viewDate.getMonth()];
    this.year = this.viewDate.getFullYear();
  }

  public nextMonth(){
    this.viewDate = new Date(new Date(this.viewDate).setMonth(this.viewDate.getMonth()+1));
    this.month = this.monthNames[this.viewDate.getMonth()];
    this.year = this.viewDate.getFullYear();
  }

  public today(){
    this.viewDate = new Date();
    this.month = this.monthNames[this.viewDate.getMonth()];
    this.year = this.viewDate.getFullYear();
  }

  public dayClicked(day) {
    console.log(day);
  }

  public eventClicked(event) {
    this.router.navigate(['/meeting', 'view', event.event._id]);
  }

  public eventTimesChanged(a): void {
    a.event.start = a.newStart;
    a.event.end = a.newEnd;
    this.updateMeeting(a);
    this.refresh.next();
  }

  private updateMeeting(event) {
    this.startLoading();
    var meeting = <Meeting> _.findWhere(this.meetings, {_id: event.event._id});
    meeting.initDate = this.toDate(event.newStart, '00:00');
    meeting.finalDate = this.toDate(event.newEnd, '00:00');
    meeting.modificationDate = new Date();

    this.MeetingsService.update(meeting)
    .subscribe(result => {
      this.openSnackBar('Meeting «' + result.name + '» updated correctly', null);
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }
}



