import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { apiUrl } from '../../variables/variables';

@Injectable()
export class UsersService {

  constructor(private http: Http) { }

  api: String = apiUrl;
  
  // Get all teams from the API
  getAll() {
    return this.http.get(this.api + '/user')
    .map(res => res.json());
  }

  // Get all teams from the API
  getAllWithoutPhoto() {
    return this.http.get(this.api + '/usersWithoutPhoto')
    .map(res => res.json());
  }

  //Get user photo
  getPhoto(id) {
    return this.http.get(this.api + '/user/photo/' + id)
    .map(res => res.json());
  }

  // Get team from
  get(id) {
    return this.http.get(this.api + '/user/' + id)
    .map(res => res.json());
  }

  // Create new team
  create(user) {
    return this.http.post(this.api + '/user', user)
    .map(res => res.json());
  }

  // Update team data
  update(user) {
    return this.http.put(this.api + '/user/' + user._id, user)
    .map(res => res.json());
  }

  // Delete team
  delete(id) {
    return this.http.delete(this.api + '/user/' + id)
    .map(res => res.json());
  }

}



