import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';
import { MdTabsModule } from '@angular/material';

// Components
import { TasksComponent } from '../../../components/task-components/tasks/tasks.component';
import { TaskComponent } from '../../../components/task-components/task/task.component'

// Modules
import { ViewTitleDirectiveModule} from '../../../modules/directive-modules/view-title-directive/view-title-directive.module';
import { DeleteConfirmationDirectiveModule } from '../../../modules/directive-modules/delete-confirmation-directive/delete-confirmation-directive.module';
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';

// Services
import { TasksService } from '../../../services/tasks/tasks.service';

const TaskComponents = [
  TaskComponent,
  TasksComponent,
];

@NgModule({
  imports: [
    AngularMaterialModule,
    CommonModule,
    ViewTitleDirectiveModule,
    FormsModule,
    DeleteConfirmationDirectiveModule,
    CapitalizePipeModule,
    MdTabsModule,
  ],
  declarations: [
    TaskComponents,
  ],
  exports: [
    TaskComponents,
  ],
  providers: [
    TasksService,
  ],
})
export class TasksModule { }



