import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

import { MdSnackBar } from '@angular/material';
import { Meeting } from '../../../classes/meeting';
import { MeetingsService } from '../../../services/meetings/meetings.service';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { meetingPlace } from '../../../enums/meetingPlace.enum';

@Component({
  selector: 'app-meeting',
  templateUrl: './meeting.component.html',
  styleUrls: ['./meeting.component.scss']
})
export class MeetingComponent implements OnInit {
  defaultPhoto: string = '../../../assets/default_image.png';
  tituloVista: string = 'Meeting';
  subtituloVista: string;
  action: string;
  meeting: Meeting = new Meeting();
  places: any = [];

  constructor(
    private MeetingsService: MeetingsService,
    private router: Router,
    private route: ActivatedRoute,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.startLoading();
    this.getPlaces();
    this.route.params.subscribe(params => {
      this.subtituloVista = this.action = params['action'];
      const kActions = {
        ['create']:() => this.completeLoading(),
        ['view']:() => this.getMeeting(params['id']),
        ['edit']:() => this.getMeeting(params['id']),
      };
      kActions[this.action]();
    });
  }


  public getPlaces() {
    var keys = Object.keys(meetingPlace);
    this.places = keys.slice(keys.length / 2);
  }

  private getMeeting(meetingID) {
    this.MeetingsService.get(meetingID)
    .subscribe(meeting => {
      console.log(meeting);
      this.meeting = meeting;
      this.meeting.creationDate = this.utcToDate(this.meeting.creationDate);
      this.meeting.initDate = this.utcToDate(this.meeting.initDate);
      this.meeting.finalDate = this.utcToDate(this.meeting.finalDate);
      this.meeting.modificationDate = this.utcToDate(this.meeting.modificationDate);
      this.meeting['creationDateString'] = this.meeting.creationDate ? this.meeting.creationDate.toLocaleString() : '-';
      this.meeting['initDateString'] = this.meeting.finalDate ? this.meeting.finalDate.toLocaleString() : '-';
      this.meeting['finalDateString'] = this.meeting.finalDate ? this.meeting.finalDate.toLocaleString() : '-';
      this.meeting['modificationDateString'] = this.meeting.modificationDate ? this.meeting.modificationDate.toLocaleString() : '-';
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private utcToDate(utc) {
    return utc ? new Date(Date.parse(utc)) : null;
  };

  public isEditForm() {
    return this.action !== 'view';
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public upload(res) {
    this.readImage(res.target);
  }

  private readImage(inputValue: any): void {
    const file: File = inputValue.files[0];
    const myReader: FileReader = new FileReader();

    myReader.onloadend = (e) => {
      this.meeting.meetingPhoto64 = myReader.result;
    };
    myReader.readAsDataURL(file);
  }

  public addTask() {
    this.openSnackBar('Operation not available', null);
  }

  public addTopic() {
    this.openSnackBar('Operation not available', null);
  }

  public addComment() {
    this.openSnackBar('Operation not available', null);
  }

  public saveMeeting() {
    const kSave = {
      ['create']:() => this.createMeeting(),
      ['edit']:() => this.updateMeeting(),
      ['view']:() => this.editMeeting(),
    };
    kSave[this.action]();
  }

  private createMeeting() {
    this.startLoading();
    this.meeting.creationDate = new Date();

    this.MeetingsService.create(this.meeting)
    .subscribe(result => {
      this.openSnackBar('Meeting «' + result.name + '» created correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private updateMeeting() {
    this.startLoading();
    this.meeting.meetingPhoto = {};
    this.meeting.modificationDate = new Date();

    this.MeetingsService.update(this.meeting)
    .subscribe(result => {
      this.openSnackBar('Meeting «' + result.name + '» updated correctly', null);
      this.completeLoading();
      this.viewMeeting();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private editMeeting() {
    this.router.navigate(['/meeting', 'edit', this.meeting._id]);
  }

  private viewMeeting() {
    this.router.navigate(['/meeting', 'view', this.meeting._id]);
  }

  public deleteMeeting(id) {
    this.startLoading();
    this.MeetingsService.delete(id)
    .subscribe(result => {
      this.openSnackBar('Meeting «' + result.name + '» deleted correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteMeeting() {
    this.openSnackBar('Cancelled operation', null);
  }

  private back() {
    this.router.navigate(['/meetings']);
  }
}



