import { Router } from '@angular/router';
import { TasksService } from '../../../services/tasks/tasks.service';
import { Component, OnInit } from '@angular/core';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import * as _ from 'underscore';
import { taskStatus } from '../../../enums/taskStatus.enum';
import { taskPriority } from '../../../enums/taskPriority.enum';

const notFound: string = 'Not found tasks';
const getting: string = 'Getting tasks';

@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  styleUrls: ['./tasks.component.scss']
})
export class TasksComponent implements OnInit {

  defaultImage: string = '../../../assets/default_task.png';
  tituloVista: string = 'Tasks';
  infoMessage: string;
  tasks: any = [];
  todoTasks: any = [];
  doingTasks: any = [];
  doneTasks: any = [];
  
  constructor(
    private TasksService: TasksService,
    private router: Router,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.infoMessage = getting;
    this.startLoading();
    this.TasksService.getAll()
    .subscribe(tasks => {
      this.infoMessage = notFound;

      this.tasks = _.groupBy(_.sortBy(tasks, function(task: any){
        return [taskStatus[task.status], taskPriority[task.priority]].join("_");
      }),'status');

      console.log(this.tasks);

      this.todoTasks = this.tasks.TODO ? this.tasks.TODO : [];      
      this.doingTasks =  this.tasks.DOING ? this.tasks.DOING : []; 
      this.doneTasks =  this.tasks.DONE ? this.tasks.DONE : []; 


      // Finish loading bar
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public actionTask(action, task) {
    var params = [
      '/task',
      action
    ];
    if(action !== 'create') {
      params = params.concat([task._id]);
    }
    this.router.navigate(params);
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public deleteTask(id) {
    this.startLoading();
    this.TasksService.delete(id)
    .subscribe(result => {
      this.tasks = _.without(this.tasks, _.findWhere(this.tasks, {_id: result._id}));
      this.openSnackBar('Task «' + result.name + '» deleted correctly', null);
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteTask() {
    this.openSnackBar('Cancelled operation', null);
  }

}



