import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MdDialog, MdDialogRef } from '@angular/material';
import { Notification } from '../../classes/notification';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import { NotificationsService } from '../../services/notifications/notifications.service';

@Component({
  selector: 'notification',
  templateUrl: 'notification.component.html',
  styleUrls: ['./notification.component.scss']
})
export class NotificationComponent {
  @Output() actionAdd: EventEmitter<any> = new EventEmitter();

  constructor(public dialog: MdDialog) { }

  public showNotification() {
    let dialogRef = this.dialog.open(NotificationDialog, {
      width: '600px',
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.actionAdd.emit();
      }
    });
  }
}

@Component({
  selector: 'notificationDialog',
  templateUrl: 'notification-dialog.component.html',
  styleUrls: ['./notification-dialog.component.scss']
})
export class NotificationDialog {
  notification: Notification = new Notification();

  constructor (
    public dialogRef: MdDialogRef<NotificationDialog>,
    private NotificationsService: NotificationsService,
    public snackBar: MdSnackBar,
    private slimLoadingBarService: SlimLoadingBarService) { }

  public addNotification(): void {
    this.startLoading();
    this.notification.creationDate = new Date();

    this.NotificationsService.create(this.notification)
    .subscribe(result => {
      this.openSnackBar('Notification «' + result.title + '» created correctly', null);
      this.completeLoading();
      this.dialogRef.close(true);
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }
}



