import { Pipe, PipeTransform } from '@angular/core';
import * as _ from 'underscore';

@Pipe({name: 'objectNgFor'})
export class ObjectNgForPipe implements PipeTransform {
  transform(value: any, args: any[]): any {
    var objRet = [];
    _.each(value, (val, index) => {
      objRet.push({
        key: index,
        value: val
      })
    });
    return objRet;
  }

}



