import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router, NavigationStart, NavigationError } from '@angular/router';
import { LoginService } from '../../services/login/login.service';
import { MdSnackBar } from '@angular/material';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent implements OnInit {
  isLogged: boolean;
  search: string;
  public userName: string;

  constructor(
    private Location: Location,
    private router: Router,
    private LoginService: LoginService,
    private snackBar: MdSnackBar,
  ) { }

  ngOnInit() {
    this.isLogged = this.LoginService.isLogged();
    this.userName = this.LoginService.getLoggedStorage().username;

    this.router.events
    .filter(event => event instanceof NavigationStart)
    .subscribe((route: NavigationStart) => {
      if(route.url === '/' && this.LoginService.isLogged()) {
        this.routeHandle();
        this.openSnackBar('(ง︡’-‘︠)ง You shall not pass', true);
      }
    });

    this.router.events
    .filter(event => event instanceof NavigationError)
    .subscribe(route => {
      this.routeHandle();
      this.openSnackBar('404:    ¯\\_(ツ)_/¯', true);
    });
  }

  private routeHandle() {
    if(this.LoginService.isLogged()) {
      this.router.navigate(['/dashboard']);
    }
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public goBack() {
    this.Location.back();
  }

   public searchText() {
    this.router.navigate(['/search', this.search]);
    this.search = null;
  }

  public logOut() {
    this.LoginService.logOut();
    this.router.navigate(['/']);
  }
}



