import { RouterModule } from '@angular/router';
// components
import { LoginComponent } from '../components/login-components/login/login.component';
import { DashboardComponent } from '../components/dashboard-components/dashboard/dashboard.component';
import { CalendarComponent } from '../components/calendar-components/calendar/calendar.component';
import { TeamsComponent } from '../components/team-components/teams/teams.component';
import { TeamComponent } from '../components/team-components/team/team.component';
import { ProjectsComponent } from '../components/project-components/projects/projects.component';
import { ProjectComponent } from '../components/project-components/project/project.component';
import { MeetingComponent } from '../components/meeting-components/meeting/meeting.component';
import { MeetingsComponent } from '../components/meeting-components/meetings/meetings.component';
import { TasksComponent } from '../components/task-components/tasks/tasks.component';
import { TaskComponent } from '../components/task-components/task/task.component';
import { MessengerComponent } from '../components/messenger-components/messenger/messenger.component';
import { ProfileComponent } from '../components/profile-components/profile/profile.component';
import { ReportsComponent } from '../components/report-components/reports/reports.component';
import { SearchComponent } from '../components/search-components/search/search.component';
import { UsersComponent } from '../components/user-components/users/users.component';
import { UserComponent } from '../components/user-components/user/user.component';


export const RouteDefinitions: RouterModule[] = [
      {
        path: '',
        component: LoginComponent,
        pathMatch: 'full',
        outlet: 'notloggedin'
      },
      {
        path: 'dashboard',
        component: DashboardComponent
      },
      {
        path: 'calendar',
        component: CalendarComponent
      },
      {
        path: 'team/:action',
        component: TeamComponent
      },
      {
        path: 'team/:action/:id',
        component: TeamComponent
      },
      {
        path: 'teams',
        component: TeamsComponent
      },
      {
        path: 'project/:action',
        component: ProjectComponent
      },
      {
        path: 'project/:action/:id',
        component: ProjectComponent
      },
      {
        path: 'projects',
        component: ProjectsComponent
      },
      {
        path: 'meeting/:action',
        component: MeetingComponent
      },
      {
        path: 'meeting/:action/:id',
        component: MeetingComponent
      },
      {
        path: 'meetings',
        component: MeetingsComponent
      },
      {
        path: 'task/:action',
        component: TaskComponent
      },
      {
        path: 'task/:action/:id',
        component: TaskComponent
      },
      {
        path: 'tasks',
        component: TasksComponent
      },
      {
        path: 'users',
        component: UsersComponent
      },
      {
        path: 'user/:action',
        component: UserComponent
      },
      {
        path: 'user/:action/:id',
        component: UserComponent
      },
      {
        path: 'messenger',
        component: MessengerComponent
      },
      {
        path: 'profile',
        component: ProfileComponent
      },
      {
        path: 'reports',
        component: ReportsComponent
      },
      {
        path: 'search/:quest',
        component: SearchComponent
      }
];



