//Angular
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CdkTableModule} from '@angular/cdk/table';
import { MdSnackBar } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';

// Services
import { DashboardService } from '../../../services/dashboard/dashboard.service';
import { NotificationsService } from '../../../services/notifications/notifications.service';

// Classes
import { Notification } from '../../../classes/notification';
import { Project } from '../../../classes/project';
import { Meeting } from '../../../classes/meeting';
import { Team } from '../../../classes/team';

// Others
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/combineLatest';
import 'rxjs/add/observable/of';
import * as _ from 'underscore';

// Consts
const notFound: string = 'Not found notifications';
const getting: string = 'Getting notifications';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  public tituloVista: string = 'Dashboard';
  public infoMessage: string = getting;
  public notifications: any = [];
  public tasks: any;
  public ChartLabels:string[] = ['Done', 'Doing', 'ToDo'];
  public ChartType:string = 'pie';
  public ChartData:number[] = [0,0,0];
  public chartOptions = {
    responsive: true
  };
  public displayedColumnsProject = ['name', 'description', 'progress'];  
  public displayedColumnsMeetings  = ['name', 'initDate']; 
  public displayedColumnsTeams  = ['name', 'initDate']; 
  
  public dataSourceProjects: any;
  public dataSourceMeetings: any;
  public dataSourceTeams: any;
  
  constructor(
    public router: Router,    
    public NotificationsService: NotificationsService,
    public snackBar: MdSnackBar,
    public slimLoadingBarService: SlimLoadingBarService,
    private DashboardService: DashboardService,
  ) { }
  
  ngOnInit() {
    this.tituloVista = 'Dashboard';
    this.startLoading();
    
    Observable.combineLatest(
      this.DashboardService.getTasks(),
      this.NotificationsService.getAll(),
      this.DashboardService.getProjects(),
      this.DashboardService.getMeetings(),
      this.DashboardService.getTeams(),      
    ).subscribe(
      data => {
        this.getTasks(data[0]);
        this.getNotifications(data[1]);
        this.getProjects(data[2]);
        this.getMeetings(data[3]);
        this.getTeams(data[4]);        
        this.completeLoading();        
      },
      error => {
        this.completeLoading();
        this.openSnackBar(error.statusText, true);
      }); 
  }

  public goToTeam(team){
    this.router.navigate(['/team', 'view', team._id]);
  }

  public goToProject(project){
    this.router.navigate(['/project', 'view', project._id]);
  }
 
  public goToMeeting(meeting){
    this.router.navigate(['/meeting', 'view', meeting._id]);
  }

  // events
  public chartClicked(e:any):void {
    console.log(e);
  }

  public chartHovered(e:any):void {
    console.log(e);
  }

  private utcToDate(utc) {
    return utc ? new Date(Date.parse(utc)) : null;
  };

  private getMeetings(meetings){
    this.dataSourceMeetings = new ExtentedDataSourceMeeting()
    this.dataSourceMeetings.data = _.sortBy(meetings, 'initDate');
  
    console.log(meetings);
  }

  private getTeams(teams){
    this.dataSourceTeams = new ExtentedDataSourceTeam()
    this.dataSourceTeams.data = teams;
  }

  private getProjects(projects){
    this.dataSourceProjects = new ExtentedDataSourceProject()
    this.dataSourceProjects.data = projects;
  }

  private getTasks(task){
      this.tasks = _.groupBy(task, 'status');
      this.ChartData = [this.tasks.DONE.length, this.tasks.DOING.length, this.tasks.TODO.length]; 
  }

  private getNotifications(notifications) {
      this.infoMessage = notFound;
      this.notifications = notifications;
      _.each(this.notifications, (notification: Notification) => {
        notification.creationDate = this.utcToDate(notification.creationDate);
        notification['creationDateString'] = notification.creationDate ? notification.creationDate.toLocaleString() : '-';
      });
  }

  public isAdmin() {
    return false;
  }

  public refreshNotification() {
    this.notifications = [];
    this.NotificationsService.getAll().subscribe(
      notifications => {
        this.getNotifications(notifications);
      }
    );
   }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }
}


export class ExtentedDataSourceProject extends DataSource<any> {
  public data: Project[];
  connect(): Observable<Project[]> {
    return Observable.of(this.data);
  }
  disconnect() {}
}

export class ExtentedDataSourceMeeting extends DataSource<any> {
  public data: Meeting[];
  connect(): Observable<Meeting[]> {
    return Observable.of(this.data);
  }
  disconnect() {}
}

export class ExtentedDataSourceTeam extends DataSource<any> {
  public data: Team[];
  connect(): Observable<Team[]> {
    return Observable.of(this.data);
  }
  disconnect() {}
}




