import { Router } from '@angular/router';
import { UsersService } from '../../../services/users/users.service';
import { trigger, transition, style, animate, state } from '@angular/animations'
import { Component, OnInit } from '@angular/core';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import * as _ from 'underscore';
import { User } from '../../../classes/user';
import { imageAnimation } from '../../../animations/animations';


const notFound: string = 'Not found users';
const getting: string = 'Getting users';

@Component({
  selector: 'app-users',  
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss'],
  animations: [imageAnimation]
})
export class UsersComponent implements OnInit {
  defaultImage: string = '../../../assets/default_user.png';
  tituloVista: string = 'Users';
  infoMessage: string;
  users: any = [];

  constructor(
    private UsersService: UsersService,
    private router: Router,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.infoMessage = getting;
    this.startLoading();
    this.UsersService.getAllWithoutPhoto()
    .subscribe(users => {
      this.infoMessage = notFound;
      this.users = users;
      this.getUserPhotos()
      console.log(users);
      // Finish loading bar
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }


  private getUserPhotos(){
    let userService = this.UsersService;
    _.each(this.users, function(user: User){
        userService.getPhoto(user._id)
        .subscribe(photo => {
          user.userPhoto64 = photo.userPhoto64;
        });
    }); 
  }

  public actionUser(action, user) {
    var params = [
      '/user',
      action
    ];
    if(action !== 'create') {
      params = params.concat([user._id]);
    }
    this.router.navigate(params);
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public deleteUser(id) {
    this.startLoading();
    this.UsersService.delete(id)
    .subscribe(result => {
      this.users = _.without(this.users, _.findWhere(this.users, {_id: result._id}));
      this.openSnackBar('User «' + result.name + '» deleted correctly', null);
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteUser() {
    this.openSnackBar('Cancelled operation', null);
  }
}



