import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';

// Components
import { MeetingsComponent } from '../../../components/meeting-components/meetings/meetings.component';
import { MeetingComponent } from '../../../components/meeting-components/meeting/meeting.component';

// Modules
import { ViewTitleDirectiveModule} from '../../../modules/directive-modules/view-title-directive/view-title-directive.module';
import { DeleteConfirmationDirectiveModule } from '../../../modules/directive-modules/delete-confirmation-directive/delete-confirmation-directive.module';

// Services
import { MeetingsService } from '../../../services/meetings/meetings.service';

// Pipes
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';
import { ObjectNgForPipeModule} from '../../../modules/pipe-modules/objectNgFor-pipe/objectNgFor-pipe.module';

const MeetingComponents = [
  MeetingComponent,
  MeetingsComponent,
];

@NgModule({
  imports: [
    AngularMaterialModule,
    CommonModule,
    ViewTitleDirectiveModule,
    FormsModule,
    DeleteConfirmationDirectiveModule,
    CapitalizePipeModule,
    ObjectNgForPipeModule,
  ],
  declarations: [
    MeetingComponents,
  ],
  exports: [
    MeetingComponents,
  ],
  providers: [
    MeetingsService,
  ],
})
export class MeetingsModule { }



