import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';

// Components
import { ReportsComponent } from '../../../components/report-components/reports/reports.component';
import { ReportCreateComponent } from '../../../components/report-components/report-create/report-create.component';
import { ReportDetailComponent } from '../../../components/report-components/report-detail/report-detail.component';

// Modules
import { ViewTitleDirectiveModule} from '../../../modules/directive-modules/view-title-directive/view-title-directive.module';
import { DeleteConfirmationDirectiveModule } from '../../../modules/directive-modules/delete-confirmation-directive/delete-confirmation-directive.module';
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';


// Services
import { ReportsService } from '../../../services/reports/reports.service';

const ReportComponents = [
  ReportCreateComponent,
  ReportDetailComponent,
  ReportsComponent,
];

@NgModule({
  imports: [
    AngularMaterialModule,
    CommonModule,
    ViewTitleDirectiveModule,
    FormsModule,
    DeleteConfirmationDirectiveModule,
    CapitalizePipeModule,
  ],
  declarations: [
    ReportComponents,
  ],
  exports: [
    ReportComponents,
  ],
  providers: [
    ReportsService,
  ],
})
export class ReportsModule { }



