import { Component, OnInit } from '@angular/core';
import { TasksService } from '../../../services/tasks/tasks.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import { Task } from '../../../classes/task';
import { taskStatus } from '../../../enums/taskStatus.enum';
import { taskPriority } from '../../../enums/taskPriority.enum';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.scss']
})
export class TaskComponent implements OnInit {
  defaultPhoto: string = '../../../assets/default_image.png';
  tituloVista: string = 'Task';
  subtituloVista: string;
  action: string;
  task: Task = new Task();
  status: any = [];
  priorities: any = [];

  constructor(
    private TasksService: TasksService,
    private router: Router,
    private route: ActivatedRoute,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.getStatus();
    this.getPriority();
    this.startLoading();
    this.route.params.subscribe(params => {
      this.subtituloVista = this.action = params['action'];
      const kActions = {
        ['create']:() => this.completeLoading(),
        ['view']:() => this.getTask(params['id']),
        ['edit']:() => this.getTask(params['id']),
      };
      kActions[this.action]();
    });
  }

  public getStatus() {
    var keys = Object.keys(taskStatus);
    this.status = keys.slice(keys.length / 2);
  }

  public getPriority() {
    var keys = Object.keys(taskPriority);
    this.priorities = keys.slice(keys.length / 2);
  }

  private getTask(taskID) {
    this.TasksService.get(taskID)
    .subscribe(task => {
      console.log(task);
      this.task = task;
      this.task.creationDate = this.utcToDate(this.task.creationDate);
      this.task.initDate = this.utcToDate(this.task.initDate);
      this.task.finalDate = this.utcToDate(this.task.finalDate);
      this.task.modificationDate = this.utcToDate(this.task.modificationDate);
      this.task['creationDateString'] = this.task.creationDate ? this.task.creationDate.toLocaleString() : '-';
      this.task['initDateString'] = this.task.initDate ? this.task.initDate.toLocaleString() : '-';
      this.task['finalDateString'] = this.task.finalDate ? this.task.finalDate.toLocaleString() : '-';
      this.task['modificationDateString'] = this.task.modificationDate ? this.task.modificationDate.toLocaleString() : '-';
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private utcToDate(utc) {
    return utc ? new Date(Date.parse(utc)) : null;
  };

  public isEditForm() {
    return this.action !== 'view';
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public addComment() {
    this.openSnackBar('Operation not available', null);
  }

  public saveTask() {
    const kSave = {
      ['create']:() => this.createTask(),
      ['edit']:() => this.updateTask(),
      ['view']:() => this.editTask(),
    };
    kSave[this.action]();
  }

  private createTask() {
    this.startLoading();
    this.task.creationDate = new Date();

    this.TasksService.create(this.task)
    .subscribe(result => {
      this.openSnackBar('Task «' + result.name + '» created correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private updateTask() {
    this.startLoading();
    this.task.modificationDate = new Date();

    this.TasksService.update(this.task)
    .subscribe(result => {
      this.openSnackBar('Task «' + result.name + '» updated correctly', null);
      this.completeLoading();
      this.viewTask();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private editTask() {
    this.router.navigate(['/task', 'edit', this.task._id]);
  }

  private viewTask() {
    this.router.navigate(['/task', 'view', this.task._id]);
  }

  public deleteTask(id) {
    this.startLoading();
    this.TasksService.delete(id)
    .subscribe(result => {
      this.openSnackBar('Task «' + result.name + '» deleted correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteTask() {
    this.openSnackBar('Cancelled operation', null);
  }

  private back() {
    this.router.navigate(['/tasks']);
  }
}



