import { trigger, state, style, transition, animate } from '@angular/core';

export const imageAnimation = trigger('imageAnimation', [
    transition(':enter', [
      style({opacity: 0}),
      animate('500ms', style({opacity: 1}))
    ]),
    transition(':leave', [
      style({opacity: 1}),
      animate('500ms', style({opacity: 0}))
    ])
  ]);


