import {Component, Input, Output, EventEmitter} from '@angular/core';
import {MdDialog, MdDialogRef} from '@angular/material';

@Component({
  selector: 'delete-confirmation',
  templateUrl: 'delete-confirmation.component.html',
  styleUrls: ['./delete-confirmation.component.scss']
})
export class DeleteConfirmationComponent {
  @Input() type: string;
  @Input() name: string;
  @Input() id: string;
  @Input() styleClass: string;
  @Output() actionYes: EventEmitter<any> = new EventEmitter();
  @Output() actionNo: EventEmitter<any> = new EventEmitter();

  constructor(public dialog: MdDialog) { }

  public delete() {
    let dialogRef = this.dialog.open(DeleteConfirmationDialog);
    dialogRef.componentInstance.type = this.type;
    dialogRef.componentInstance.name = this.name;
    dialogRef.componentInstance.id = this.id;
    dialogRef.afterClosed().subscribe(result => {
      if(result === 'S') {
        this.actionYes.emit(this.id);
      } else {
        this.actionNo.emit(this.id);
      }
    });
  }
}

@Component({
  selector: 'deleteConfirmationDialog',
  templateUrl: 'delete-confirmation-dialog.component.html',
  styleUrls: ['./delete-confirmation-dialog.component.scss']
})
export class DeleteConfirmationDialog {
  public type: string;
  public name: string;
  public id: string;

  constructor(public dialogRef: MdDialogRef<DeleteConfirmationDialog>) { }
}



