import { Component, OnInit } from '@angular/core';
import { ProjectsService } from '../../../services/projects/projects.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import { Project } from '../../../classes/project';

@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.scss']
})
export class ProjectComponent implements OnInit {
  defaultPhoto: string = '../../../assets/default_image.png';
  tituloVista: string = 'Project';
  subtituloVista: string;
  action: string;
  project: Project = new Project();

  constructor(
    private ProjectsService: ProjectsService,
    private router: Router,
    private route: ActivatedRoute,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.startLoading();
    this.route.params.subscribe(params => {
      this.subtituloVista = this.action = params['action'];
      const kActions = {
        ['create']:() => this.completeLoading(),
        ['view']:() => this.getProject(params['id']),
        ['edit']:() => this.getProject(params['id']),
      };
      kActions[this.action]();
    });
  }

  private getProject(projectID) {
    this.ProjectsService.get(projectID)
    .subscribe(project => {
      console.log(project);
      this.project = project;
      this.project.creationDate = this.utcToDate(this.project.creationDate);
      this.project.finalDate = this.utcToDate(this.project.finalDate);
      this.project.modificationDate = this.utcToDate(this.project.modificationDate);
      this.project['creationDateString'] = this.project.creationDate ? this.project.creationDate.toLocaleString() : '-';
      this.project['finalDateString'] = this.project.finalDate ? this.project.finalDate.toLocaleString() : '-';
      this.project['modificationDateString'] = this.project.modificationDate ? this.project.modificationDate.toLocaleString() : '-';
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private utcToDate(utc) {
    return utc ? new Date(Date.parse(utc)) : null;
  };

  public isEditForm() {
    return this.action !== 'view';
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public upload(res) {
    this.readImage(res.target);
  }

  private readImage(inputValue: any): void {
    const file: File = inputValue.files[0];
    const myReader: FileReader = new FileReader();

    myReader.onloadend = (e) => {
      this.project.projectPhoto64 = myReader.result;
    };
    myReader.readAsDataURL(file);
  }

  public addTask() {
    this.openSnackBar('Operation not available', null);
  }

  public addTeam() {
    this.openSnackBar('Operation not available', null);
  }

  public saveProject() {
    const kSave = {
      ['create']:() => this.createProject(),
      ['edit']:() => this.updateProject(),
      ['view']:() => this.editProject(),
    };
    kSave[this.action]();
  }

  private createProject() {
    this.startLoading();
    this.project.creationDate = new Date();

    this.ProjectsService.create(this.project)
    .subscribe(result => {
      this.openSnackBar('Project «' + result.name + '» created correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private updateProject() {
    this.startLoading();
    this.project.projectPhoto = {};
    this.project.modificationDate = new Date();

    this.ProjectsService.update(this.project)
    .subscribe(result => {
      this.openSnackBar('Project «' + result.name + '» updated correctly', null);
      this.completeLoading();
      this.viewProject();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private editProject() {
    this.router.navigate(['/project', 'edit', this.project._id]);
  }

  private viewProject() {
    this.router.navigate(['/project', 'view', this.project._id]);
  }

  public deleteProject(id) {
    this.startLoading();
    this.ProjectsService.delete(id)
    .subscribe(result => {
      this.openSnackBar('Project «' + result.name + '» deleted correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteProject() {
    this.openSnackBar('Cancelled operation', null);
  }

  private back() {
    this.router.navigate(['/projects']);
  }
}



