import { Router } from '@angular/router';
import { MeetingsService } from '../../../services/meetings/meetings.service';
import { Component, OnInit } from '@angular/core';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import { Meeting } from '../../../classes/meeting';
import { MonthNames } from '../../../enums/monthNames.enum';
import * as _ from 'underscore';

const notFound: string = 'Not found meetings';
const getting: string = 'Getting meetings';

@Component({
  selector: 'app-meetings',
  templateUrl: './meetings.component.html',
  styleUrls: ['./meetings.component.scss']
})
export class MeetingsComponent implements OnInit {

  defaultImage: string = '../../../assets/default_meeting.png';
  tituloVista: string = 'Meetings';
  infoMessage: string;
  meetings: any = [];
  monthNames = MonthNames;
  meetingsGroup: any = [];

  constructor(
    private MeetingsService: MeetingsService,
    private router: Router,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.infoMessage = getting;
    this.startLoading();
    this.MeetingsService.getAll()
    .subscribe(meetings => {
      this.infoMessage = notFound;
      this.meetings = _.sortBy(meetings, 'initDate');
      _.each(this.meetings, (meeting: Meeting) => {
        meeting.initDate = this.utcToDate(meeting.initDate);
        meeting['initDateString'] = meeting.initDate ? meeting.initDate.toLocaleDateString() : '-';
        meeting.finalDate = this.utcToDate(meeting.finalDate);
        meeting['finalDateString'] = meeting.finalDate ? meeting.finalDate.toLocaleDateString() : '-';
      });
      // Group by month and year
      this.meetingsGroup =  _.groupBy(this.meetings, (meeting: Meeting) => {
        return [this.monthNames[meeting.initDate ? meeting.initDate.getMonth() : 0], meeting.initDate ? meeting.initDate.getFullYear() : 0].join(' ');
      });
      // Finish loading bar
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private utcToDate(utc) {
    return utc ? new Date(Date.parse(utc)) : null;
  };

  public actionMeeting(action, meeting) {
    var params = [
      '/meeting',
      action
    ];
    if(action !== 'create') {
      params = params.concat([meeting._id]);
    }
    this.router.navigate(params);
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public deleteMeeting(id) {
    this.startLoading();
    this.MeetingsService.delete(id)
    .subscribe(result => {
      this.meetings = _.without(this.meetings, _.findWhere(this.meetings, {_id: result._id}));
      this.openSnackBar('Meeting «' + result.name + '» deleted correctly', null);
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteMeeting() {
    this.openSnackBar('Cancelled operation', null);
  }

}



