import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';

// Components
import { MessengerComponent } from '../../../components/messenger-components/messenger/messenger.component';
import { ViewTitleDirectiveModule} from '../../directive-modules/view-title-directive/view-title-directive.module';

// Services
import { MessengerService } from '../../../services/messenger/messenger.service';

// Pipes
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';


@NgModule({
  imports: [
    AngularMaterialModule, 
    CommonModule,
    ViewTitleDirectiveModule,
    CapitalizePipeModule,
  ],
  declarations: [
    MessengerComponent,
  ],
  exports: [
    MessengerComponent,
  ],
  providers: [
    MessengerService,
  ],
})
export class MessengerModule { }



