import { Component, OnInit } from '@angular/core';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import { Observable } from 'rxjs/Observable';
import { DataSource } from '@angular/cdk/collections';
import 'rxjs/add/observable/combineLatest';
import 'rxjs/add/observable/of';

//Services
import { ProfileService } from '../../../services/profile/profile.service';
import { LoginService } from '../../../services/login/login.service';

// Classes
import { Project } from '../../../classes/project';
import { Meeting } from '../../../classes/meeting';
import { Team } from '../../../classes/team';
import { Task } from '../../../classes/task';
import { User } from '../../../classes/user';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  defaultPhoto: string = '../../../assets/default_image.png';
  
  public tituloVista: string;
  public dataSourceProjects: any;
  public dataSourceMeetings: any;
  public dataSourceTeams: any;
  public dataSourceTasks: any;
  
  public displayedColumnsProjects = ['name', 'description', 'progress'];  
  public displayedColumnsMeetings  = ['name', 'initDate']; 
  public displayedColumnsTeams  = ['name', 'initDate']; 
  public displayedColumnsTasks  = ['name', 'initDate', 'finalDate']; 

  public user: User;
  
  constructor(
    public snackBar: MdSnackBar,
    public slimLoadingBarService: SlimLoadingBarService,
    private ProfileService: ProfileService,    
    private LoginService: LoginService,    
  ) { }

  ngOnInit() {
    this.tituloVista = 'Profile';
    this.user = this.LoginService.getLoggedStorage();
    
    this.startLoading();
    
    Observable.combineLatest(
      this.ProfileService.getUserMeetings(this.user._id),      
      this.ProfileService.getUserTasks(this.user._id),
      this.ProfileService.getUserTeams(this.user._id),
      this.ProfileService.getUser(this.user._id),
       // this.ProfileService.getUserProjects(this.user._id),      

    ).subscribe(
      data => {  
        console.log(data[0]);   
        console.log(data[1]);   
        console.log(data[2]);   

        this.getMeetings(data[0]);        
        this.getTasks(data[1]);
        this.getTeams(data[2]);
        this.getUser(data[3]);
        
                // this.getProjects(data[4]);

        this.completeLoading();        
      },
      error => {
        this.completeLoading();
        this.openSnackBar(error.statusText, true);
      }); 
  }

  private getUser(user){
    this.user = user;
    console.log(user);
  }

  private getTeams(teams){
    this.dataSourceTeams = new ExtentedDataSourceTeam()
    this.dataSourceTeams.data = teams;
  }

  
  private getProjects(teams){
    this.dataSourceProjects = new ExtentedDataSourceTeam()
    this.dataSourceProjects.data = teams;
  }

  
  private getMeetings(meetings){
    this.dataSourceMeetings = new ExtentedDataSourceTeam()
    this.dataSourceMeetings.data = meetings;
  }

  
  private getTasks(tasks){
    this.dataSourceTasks = new ExtentedDataSourceTeam()
    this.dataSourceTasks.data = tasks;
  }


  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

}


export class ExtentedDataSourceProject extends DataSource<any> {
  public data: Project[];
  connect(): Observable<Project[]> {
    return Observable.of(this.data);
  }
  disconnect() {}
}

export class ExtentedDataSourceMeeting extends DataSource<any> {
  public data: Meeting[];
  connect(): Observable<Meeting[]> {
    return Observable.of(this.data);
  }
  disconnect() {}
}

export class ExtentedDataSourceTeam extends DataSource<any> {
  public data: Team[];
  connect(): Observable<Team[]> {
    return Observable.of(this.data);
  }
  disconnect() {}
}

export class ExtentedDataSourceTask extends DataSource<any> {
  public data: Task[];
  connect(): Observable<Task[]> {
    return Observable.of(this.data);
  }
  disconnect() {}
}




