import { Component, OnInit } from '@angular/core';
import { UsersService } from '../../../services/users/users.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { MdSnackBar } from '@angular/material';
import { User } from '../../../classes/user';
import { userRank } from '../../../enums/userRank.enum';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {
  defaultPhoto: string = '../../../assets/default_user.png';
  tituloVista: string = 'User';
  subtituloVista: string;
  action: string;
  user: User = new User();
  ranks: any = [];

  constructor(
    private UsersService: UsersService,
    private router: Router,
    private route: ActivatedRoute,
    private slimLoadingBarService: SlimLoadingBarService,
    public snackBar: MdSnackBar) { }

  ngOnInit() {
    this.startLoading();
    this.getRank();
    this.route.params.subscribe(params => {
      this.subtituloVista = this.action = params['action'];
      const kActions = {
        ['create']:() => this.completeLoading(),
        ['view']:() => this.getUser(params['id']),
        ['edit']:() => this.getUser(params['id']),
      };
      kActions[this.action]();
    });
  }

  public getRank() {
    var keys = Object.keys(userRank);
    this.ranks = keys.slice(keys.length / 2);
  }

  private getUser(userID) {
    this.UsersService.get(userID)
    .subscribe(user => {
      console.log(user);
      this.user = user;
      this.user.creationDate = this.utcToDate(this.user.creationDate);
      this.user.modificationDate = this.utcToDate(this.user.modificationDate);
      this.user['creationDateString'] = this.user.creationDate ? this.user.creationDate.toLocaleString() : '-';
      this.user['modificationDateString'] = this.user.modificationDate ? this.user.modificationDate.toLocaleString() : '-';
      this.completeLoading();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private utcToDate(utc) {
    return utc ? new Date(Date.parse(utc)) : null;
  };

  public isEditForm() {
    return this.action !== 'view';
  }

  private startLoading() {
    this.slimLoadingBarService.start();
  }

  private completeLoading() {
    setTimeout(() => {
      this.slimLoadingBarService.complete();
    });
  }

  private openSnackBar(message, error) {
    this.snackBar.open(message, null, {
      duration: 5000,
      extraClasses: !error ? ['snack-bar'] : ['snack-bar-error']
    });
  }

  public upload(res) {
    this.readImage(res.target);
  }

  private readImage(inputValue: any): void {
    const file: File = inputValue.files[0];
    const myReader: FileReader = new FileReader();

    myReader.onloadend = (e) => {
      this.user.userPhoto64 = myReader.result;
    };
    myReader.readAsDataURL(file);
  }

  public saveUser() {
    const kSave = {
      ['create']:() => this.createUser(),
      ['edit']:() => this.updateUser(),
      ['view']:() => this.editUser(),
    };
    kSave[this.action]();
  }

  private createUser() {
    this.startLoading();
    this.user.creationDate = new Date();

    this.UsersService.create(this.user)
    .subscribe(result => {
      this.openSnackBar('User «' + result.name + '» created correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private updateUser() {
    this.startLoading();
    this.user.userPhoto = {};
    this.user.modificationDate = new Date();

    this.UsersService.update(this.user)
    .subscribe(result => {
      this.openSnackBar('User «' + result.name + '» updated correctly', null);
      this.completeLoading();
      this.viewUser();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  private editUser() {
    this.router.navigate(['/user', 'edit', this.user._id]);
  }

  private viewUser() {
    this.router.navigate(['/user', 'view', this.user._id]);
  }

  public deleteUser(id) {
    this.startLoading();
    this.UsersService.delete(id)
    .subscribe(result => {
      this.openSnackBar('User «' + result.name + '» deleted correctly', null);
      this.completeLoading();
      this.back();
    }, error => {
      this.completeLoading();
      this.openSnackBar(error.statusText, true);
    });
  }

  public cancelDeleteUser() {
    this.openSnackBar('Cancelled operation', null);
  }

  private back() {
    this.router.navigate(['/users']);
  }

}



