import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AngularMaterialModule } from '../../angular-material/angular-material.module';
import { SlimLoadingBarModule } from 'ng2-slim-loading-bar';

// Components
import { LoginComponent } from '../../../components/login-components/login/login.component';

// Directives
import { ViewTitleDirectiveModule} from '../../directive-modules/view-title-directive/view-title-directive.module';

// Services
import { LoginService } from '../../../services/login/login.service';

// Pipes
import { CapitalizePipeModule} from '../../../modules/pipe-modules/capitalize-pipe/capitalize-pipe.module';


@NgModule({
  imports: [
    AngularMaterialModule,
    SlimLoadingBarModule,
    CommonModule,
    ViewTitleDirectiveModule,
    FormsModule,
    CapitalizePipeModule,
  ],
  declarations: [
    LoginComponent,
  ],
  exports: [
    LoginComponent,
  ],
  providers: [
    LoginService,
  ],
})
export class LoginModule { }



